.Destination{
    margin:4rem 6rem;
}

.Destination h1{
    font-size: 3rem;
}

.first-des{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
   
}
.second-des{
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 4.5rem;
   
}


.des-text{
    width:45%;
    text-align: start;
    font-size: 1.1rem;
    
}

.des-text h2{
    padding-bottom: 1rem;
}

.image{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;

}

.image img{
    width:49%;
    height: 350px;
    border-radius: 6px;
    box-shadow:-1px 1px 62px -18px rgba(0,0,0,0.19) ;
    
}

.image img:last-child{
    position:absolute;
    top:-10%;
    right:0;
}

@media screen and (max-width:850px) {

    .Destination{
        margin: 4rem 2rem;
    }
    .first-des{
        display: flex;
        flex-direction: column-reverse;
  
        margin-top: 3.5rem;
       
    }
    .second-des{
        display: flex;
        flex-direction: column-reverse;
  
        margin-top: 3.5rem;
       
    }
    

    .des-text{
        width:100%
    }
    .image{
        width: 100%;
        margin: 1.8rem 0;
    }
    
    .image img{
        height: 250px;
    }
}