.form-container{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.form-container form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}

.form-container input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solidsilver;
}
.form-container textarea{
   
    padding: 0 1rem;
    margin-bottom: 1rem;
    border-radius: .3rem;
    border: 1px solidsilver;
}

.form-container button{
    display: inline;
}

@media screen and (max-width:850px) {
    .form-container{
        margin: 4rem 2rem;
        color: #2a2a2a;
    }
    .form-container form{
        padding-top: 2rem;
   
        width: 100%;
       
        
    }
    .form-container input{
        font-size: 1.1rem;
    
        
    }
  


    
}